/* eslint-disable no-lone-blocks */
import React, { Suspense, lazy } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { LayoutSplashScreen } from "../../../_metronic";


{
	/* =============Dashboard============= */
}
const Dashboard = lazy(() => import("../dashboard/Dashboard"));

{
	/* =============User Page============= */
}
const AddEditUser = lazy(() =>
	import("../user/AddEditUser")
);
const ListUser = lazy(() =>
	import("../user/ListUser")
);

{
	/* =============Customer Page============= */
}

const ListCustomer = lazy(() =>
	import("../customer/ListCustomer")
);



{
	/* =============Error Page============= */
}
const Error403 = lazy(() => import("../common/Error403"));
{
	/* ==================================== */
}

export default function HomePage() {
	return (
		<Suspense fallback={<LayoutSplashScreen />}>
			<Switch>
				{
					/* Redirect from root URL to /dashboard. */
					<Redirect exact from="/admin" to="/admin/dashboard" />
				}


				{/* Route User */}
				<Route
					path="/admin/user/add"
					exact
					component={AddEditUser}
				/>

				<Route
					path="/admin/user/update/:id"
					component={AddEditUser}
					exact
				/>
				<Route exact path="/admin/user/list" component={ListUser} />

				{/* Route other */}
				<Route exact path="/admin/customer/list" component={ListCustomer} />

				{/* Route other */}
				<Route exact path="/admin/dashboard" component={Dashboard} />


				{/* <Redirect to="Error403" /> */}
				<Route exact path="/admin/Error403" component={Error403} />
			</Switch>
		</Suspense>
	);
}
