import React from "react";
import { Link, Switch, Route, Redirect } from "react-router-dom";
import { toAbsoluteUrl } from "../../../_metronic";
import "../../../_metronic/_assets/sass/pages/login/login-1.scss";
import Login from "./Login";


export default function AuthPage() {
  return (
    <>
      <div className="kt-grid kt-grid--ver kt-grid--root">
        <div
          id="kt_login"
          className="kt-grid kt-grid--hor kt-grid--root kt-login kt-login--v1"
        >
          <div className="kt-grid__item kt-grid__item--fluid kt-grid kt-grid--desktop kt-grid--ver-desktop kt-grid--hor-tablet-and-mobile">
            <div
              className="kt-grid__item kt-grid__item--order-tablet-and-mobile-2 kt-grid kt-grid--hor kt-login__aside img-center"
              style={{
                backgroundImage: `url(${toAbsoluteUrl("/media/bg/bg-1.jpg")})`
              }}
            >
              <div className="kt-grid__item">
                <Link to="/admin" className="kt-login__logo">
                  <img
                    alt="Logo"
                    src={toAbsoluteUrl("/images/survey.png")}
                  />
                </Link>
                <h2 className="kt-login__title1">TCOM - Tech For Life</h2>
              </div>

            </div>

            <div className="kt-grid__item kt-grid__item--fluid  kt-grid__item--order-tablet-and-mobile-1  kt-login__wrapper">

              <Switch>
                <Route exact path="/admin/auth/login" component={Login} />

                <Redirect from="/admin/auth" exact={true} to="/admin/auth/login" />
                <Redirect exact to="/admin/auth/login" />
              </Switch>
            </div>

          </div>
        </div>
      </div>
    </>
  );
}
